<template>
  <menuBox @moveSignal="move" />
  <div class="loginPageContainer">
    <form>
      <input
        class="forms"
        v-model="this.registerInformations.email"
        placeholder="이메일"
      />

      <input
        class="forms"
        v-model="this.registerInformations.name"
        placeholder="이름"
      />
      <input
        class="forms"
        v-model="this.registerInformations.id"
        placeholder="아이디"
      />

      <input
        class="forms"
        v-model="this.registerInformations.password"
        placeholder="비밀번호"
        type="password"
      />
      <input
        class="forms"
        v-model="this.registerInformations.passwordConfirm"
        placeholder="비밀번호 확인"
        tyle="password"
      />
      <br />
      <button class="registerButton" @click="register">회원 가입</button>
    </form>
    <div class="signupLinkContainer"><a>계정이 이미 있나요?</a></div>
    <br />
    <router-link
      to="/login"
      style="font-size: 20px; text-decoration: none; color: #0e0c32"
      >로그인</router-link
    >
  </div>
</template>

<script>
import menuBox from "./menuBox.vue";
export default {
  name: "loginPage",
  data() {
    return {
      registerInformations: {
        id: "",
        password: "",
        passwordConfirm: "",
        name: "",
        email: "",
      },
    };
  },

  components: {
    menuBox,
  },
  mounted() {},
  methods: {
    register() {
      if (
        this.registerInformations.password !=
        this.registerInformations.passwordConfirm
      ) {
        alert("비밀번호를 다시 확인해 주세요");
      } else {
        this.$axios
          .post("/register", this.registerInformations)
          .then((res) => {
            alert(res.data);
          })
          .catch(function (error) {
            alert(error);
          });
      }
    },
  },
};
</script>

