  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.forms {
  display: flex;
  border-radius: 12px;
  margin-top: 10px;
  height: 60px;
  width: 300px;
  font-size: 20px;
  padding-left: 12px;
}
.registerButton {
  height: 60px;
  width: 300px;
  border-radius: 12px;
  border: none;
  display: flex;
  margin: 0 auto;
  margin-top: 20px;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  background: #0e0c32;
  color: white;
}
.signupLinkContainer {
  display: flex;
