<template>
  <menuBox @moveSignal="move" />
  <div class="loginPageContainer">
    <form>
      <input
        class="forms"
        v-model="loginInformations.id"
        placeholder="아이디"
      />

      <input
        class="forms"
        v-model="loginInformations.password"
        placeholder="비밀번호"
        type="password"
      />
      <br />
      <button class="loginButton" @click="login">로그인</button>
    </form>
    <div class="signupLinkContainer"><a>계정이 없나요?</a></div>
    <br />
    <router-link
      to="/register"
      style="font-size: 20px; text-decoration: none; color: #0e0c32"
      >회원 가입</router-link
    >
  </div>
</template>

<script>
import menuBox from "./menuBox.vue";
export default {
  name: "loginPage",
  data() {
    return {
      loginInformations: {
        id: "",
        password: "",
      },
    };
  },

  components: {
    menuBox,
  },
  mounted() {},
  methods: {
    login() {
      // 엔터치면 alert안나온다

      this.$axios
        .post("/login", this.loginInformations)
        .then((res) => {
          if (res.data == 1) {
            alert("로그인 성공");
          } else if (res.data == 2) {
            alert("비밀번호 확인");
          } else {
            alert("아이디 확인");
          }
        })
        .catch(function (error) {
          alert(error);
        });
    },
  },
};
</script>

