.loginPageContainer {
  flex-direction: column; /* 아이템들을 세로로 나열 */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.forms {
  border-radius: 12px;
  height: 75px;
  width: 500px;
  font-size: 20px;
  padding-left: 12px;
}
.loginButton {
  height: 60px;
  width: 300px;
  border-radius: 12px;
  border: none;
  display: flex;
  margin: 0 auto;
  margin-top: 20px;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  background: #0e0c32;
  color: white;
}
.signupLinkContainer {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}
</style>
